  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_Regular.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_Regular.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_Regular.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_Regular.eot") format("eot");
