.news__wrapper, .help {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 24px;
}

.news-article, .help-element, .about-us__content, .statistics__description-group {
  display: flex;
  flex-direction: column;
}

.intro__slide, .site-nav__link, .actions-menu, .header__logo {
  display: flex;
  align-items: center;
}

.news__header, .statistics__description-list, .site-nav, .header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.download-catalog, .statistics__description-group:before, .btn--link:after, .site-nav__link--subnav:after, .search-btn:before, .actions-menu__link--feedback:before {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.main-menu__link:hover, .btn--link:hover, .site-nav__item:hover .site-nav__link:hover, .actions-menu__search:hover, .actions-menu__link--feedback:hover:before, .actions-menu__link:hover, .header__logo:hover {
  opacity: 0.7;
}
.main-menu__link:active, .btn--link:active, .site-nav__item:hover .site-nav__link:active, .actions-menu__search:active, .actions-menu__link--feedback:active:before, .actions-menu__link:active, .header__logo:active {
  opacity: 0.5;
}

body {
  margin: 0;
  background-color: #fff;
  font-family: "Golos-Text", sans-serif;
  font-size: 15px;
  line-height: 1.74;
  font-weight: 400;
  color: #404040;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.page {
  overflow: hidden;
}

.bg-accent {
  background-color: #003154;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.header {
  position: relative;
}
.header__wrapper {
  height: 82px;
}
@media (max-width: 414px) {
  .header__wrapper {
    padding-top: 3px;
    height: 48px;
  }
}
.header__logo {
  max-width: 520px;
  margin-right: 24px;
  transition: opacity 0.1s ease;
}
.header__logo.logo {
  width: auto;
}
.header__logo > .header__logo-img {
  width: 200px;
  height: 32px;
}
@media (max-width: 414px) {
  .header__logo > .header__logo-img {
    width: 129px;
    height: 20px;
  }
}
.header__logo-img {
  margin-right: 24px;
}
.header__logo-text {
  font-size: 11px;
  line-height: 1.58;
  color: #8F9090;
}
@media (max-width: 575px) {
  .header__logo-text {
    display: none;
  }
}
.header__form {
  width: 100%;
  background-color: #fff;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}
.header__form.active {
  opacity: 1;
  pointer-events: all;
}
.header__form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}
@media (max-width: 767px) {
  .header__form-wrapper {
    padding: 40px;
  }
}
@media (max-width: 414px) {
  .header__form-wrapper {
    padding: 30px;
  }
}

.logo {
  width: 200px;
  height: 32px;
}

.actions-menu__list {
  display: flex;
}
@media (max-width: 991px) {
  .actions-menu__list {
    display: none;
  }
}
.actions-menu__link {
  margin-right: 32px;
  font-size: 14px;
  line-height: 1.58;
  color: #003154;
  transition: opacity 0.1s ease;
}
.actions-menu__link--feedback {
  padding-left: 23px;
  position: relative;
}
.actions-menu__link--feedback:before {
  content: "";
  display: block;
  width: 17px;
  height: 20px;
  margin-top: -1px;
  background-image: url("../images/letter-icon.svg");
  position: absolute;
  top: 0;
  left: 0;
}
.actions-menu__search {
  position: relative;
}
@media (max-width: 991px) {
  .actions-menu__search {
    margin-right: 20px;
  }
}
.actions-menu__search.active .actions-menu__search-line {
  display: block;
}
.actions-menu__search-line {
  display: none;
  width: 20px;
  height: 2px;
  background: #1E1E1E;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(-50%);
}
.actions-menu__search-line:nth-child(2), .actions-menu__search-line:nth-child(3) {
  transform-origin: center;
}
.actions-menu__search-line:nth-child(2) {
  transform: rotate(45deg);
}
.actions-menu__search-line:nth-child(3) {
  transform: rotate(-45deg);
}

.search-btn {
  padding: 0;
  border: 0;
  background: none;
  cursor: pointer;
}
.search-btn:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("../images/search-icon.svg");
}
.search-btn.active:before {
  background-image: none;
}

.burger {
  display: none;
  padding: 2px 1px 2px 5px;
  background: none;
  border: 0;
  cursor: pointer;
  position: relative;
  font-size: 0;
  color: transparent;
}
@media (max-width: 991px) {
  .burger {
    display: block;
  }
}
@media (max-width: 414px) {
  .burger {
    padding-left: 1px;
  }
}
.burger__item {
  display: block;
  width: 22px;
  height: 2px;
  margin-right: 1px;
  background-color: #1E1E1E;
  border-radius: 1px;
  transition: transform 0.2s ease-out;
}
.burger__item:nth-child(2) {
  margin: 5px 0;
}
@media (max-width: 414px) {
  .burger__item {
    width: 18px;
  }
}

.site-nav {
  height: 54px;
}
@media (max-width: 991px) {
  .site-nav {
    height: 66px;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}
@media (max-width: 575px) {
  .site-nav {
    margin-right: -15px;
  }
}
.site-nav__item {
  height: 100%;
  flex-shrink: 0;
  position: relative;
}
@media (max-width: 575px) {
  .site-nav__item {
    padding-top: 11px;
    margin-right: 15px;
  }
}
.site-nav__item:not(:last-child) {
  margin-right: 30px;
}
@media (max-width: 991px) {
  .site-nav__item:not(:last-child) {
    margin-right: 10px;
  }
}
.site-nav__item:hover > .subnav {
  display: block;
}
@media (max-width: 991px) {
  .site-nav__item:hover > .subnav {
    display: none;
  }
}
.site-nav__link {
  padding-bottom: 1rem;
  padding-top: 1rem;
  font-size: 15px;
  line-height: 1.48;
  transition: opacity 0.1s ease;
}
@media (max-width: 991px) {
  .site-nav__link {
    padding: 9px 12px;
    font-size: 14px;
    line-height: 1.58;
    background: #F7F7F8;
    border-radius: 4px;
  }
}
.site-nav__link--subnav {
  padding-right: 15px;
  position: relative;
}
.site-nav__link--subnav:after {
  content: "";
  display: block;
  width: 10px;
  height: 6px;
  background-image: url("../images/arrow-icon.svg");
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
}
@media (max-width: 991px) {
  .site-nav__link--subnav {
    padding-right: 12px;
  }
  .site-nav__link--subnav:after {
    display: none;
  }
}

.subnav {
  display: none;
  width: 200px;
  background: #fff;
  box-shadow: 0 6px 8px rgba(0, 49, 84, 0.2);
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 1000;
}
.subnav__item {
  transition: background 0.2s ease-in-out;
}
.subnav__item:hover {
  background: #F2F6FB;
}
.subnav__link {
  display: block;
  padding: 8px;
  font-size: 14px;
}

.form-search {
  position: relative;
}
.form-search__input {
  display: block;
  width: 300px;
  padding: 15px;
  background: #fff;
  background: #F7F7F8;
  border-radius: 2px;
  border: 1px solid #D0D0D0;
}
.form-search__input:focus {
  outline: 0;
  border-color: #1E1E1E;
}
@media (max-width: 767px) {
  .form-search__input {
    padding: 10px;
    font-size: 14px;
  }
}
.form-search__btn {
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
}

.intro {
