.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 0;
  font-family: inherit;
  font-size: 15px;
  line-height: 1.48;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  background-color: transparent;
  border: 0;
  cursor: pointer;
  transition: background-size 0.5s, color 0.5s;
}
.btn--standard {
  padding-top: 11px;
  padding-bottom: 11px;
  border: 1px solid #003154;
  border-radius: 2px;
}
.btn--primary {
  padding-left: 58px;
  padding-right: 58px;
  background-image: linear-gradient(#003154, #003154);
  background-repeat: no-repeat;
  background-position: 0% 50%;
  background-size: 0% 100%;
  color: #003154;
}
.btn--primary:hover {
  color: #fff;
  background-color: #F7F7F8;
  background-size: 100% 100%;
}
.btn--primary:active {
  color: #fff;
  background-color: #003154;
}
@media (max-width: 414px) {
  .btn--primary {
    padding-left: 38px;
    padding-right: 38px;
  }
}
.btn--second {
  padding-left: 31px;
  padding-right: 31px;
  color: #fff;
  background-color: #003154;
}
.btn--link {
  padding-right: 16px;
  padding-bottom: 2px;
  color: #1E1E1E;
  position: relative;
}
.btn--link:before {
  content: "";
  width: 0;
  height: 1px;
  background-color: #1E1E1E;
  position: absolute;
  bottom: 0;
  left: 0;
  transition: width 0.2s linear;
}
.btn--link:after {
  content: "";
  width: 10px;
  height: 6px;
  background-image: url("../images/arrow-icon.svg");
  position: absolute;
  top: 9px;
  right: 0;
  z-index: 1;
  transform: rotate(-90deg);
}
.btn--link:hover:before, .btn--link:active:before {
  width: 100%;
}

