.statistics__description-group {
  position: relative;
  z-index: 1;
}
.statistics__description-group:before {
  content: "";
  position: absolute;
  z-index: -1;
}
@media (max-width: 991px) {
  .statistics__description-group {
    flex-basis: calc((100% - 20px) / 2);
    align-items: center;
    margin-bottom: 70px;
  }
}
@media (max-width: 575px) {
  .statistics__description-group {
    width: 100%;
    margin-bottom: 40px;
  }
}
@media (max-width: 414px) {
  .statistics__description-group {
    margin-bottom: 0;
  }
  .statistics__description-group:not(:last-child) {
    margin-bottom: 33px;
  }
}
.statistics__description-group--turnover:before {
  width: 94px;
  height: 96px;
  background-image: url("../images/rhombus.svg");
  top: -14px;
  left: -47px;
}
@media (max-width: 1399px) {
  .statistics__description-group--turnover:before {
    width: 79px;
    height: 80px;
    top: 0;
    left: -10px;
  }
}
@media (max-width: 991px) {
  .statistics__description-group--turnover:before {
    top: -12px;
    left: 70px;
  }
}
@media (max-width: 575px) {
  .statistics__description-group--turnover:before {
    left: 127px;
  }
}
.statistics__description-group--amount:before {
  width: 84px;
  height: 80px;
  background-image: url("../images/heart.svg");
  top: -8px;
  left: -40px;
}
@media (max-width: 1399px) {
  .statistics__description-group--amount:before {
    width: 72px;
    height: 68px;
    top: 0px;
    left: -7px;
  }
}
@media (max-width: 991px) {
  .statistics__description-group--amount:before {
    top: -6px;
    left: 76px;
  }
}
@media (max-width: 575px) {
  .statistics__description-group--amount:before {
    top: 1px;
    left: 78px;
  }
}
.statistics__description-group--area:before {
  width: 80px;
  height: 80px;
  background-image: url("../images/circle.svg");
  top: -8px;
  left: -42px;
}
@media (max-width: 1399px) {
  .statistics__description-group--area:before {
    width: 66px;
    height: 66px;
    top: 0;
    left: -7px;
  }
}
@media (max-width: 991px) {
  .statistics__description-group--area:before {
    top: -6px;
    left: 76px;
  }
}
@media (max-width: 575px) {
  .statistics__description-group--area:before {
    top: -3px;
    left: 133px;
  }
}
.statistics__description-group--countries:before {
  width: 88px;
  height: 71px;
  background-image: url("../images/crystal.svg");
  top: -1px;
  left: -49px;
}
@media (max-width: 1399px) {
  .statistics__description-group--countries:before {
    width: 75.5px;
    height: 61px;
    top: 0;
    left: -23px;
  }
}
@media (max-width: 991px) {
  .statistics__description-group--countries:before {
    top: -6px;
    left: 76px;
  }
}
@media (max-width: 575px) {
  .statistics__description-group--countries:before {
    top: 7px;
  }
}
.statistics__description-term {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 51px;
  line-height: 1.1;
}
@media (max-width: 414px) {
  .statistics__description-term {
    margin-bottom: 0;
    font-size: 40px;
    text-align: center;
  }
}
.statistics__description-term span {
  font-weight: 700;
  font-size: 23px;
}
@media (max-width: 414px) {
  .statistics__description-term span {
    font-size: 20px;
    font-weight: 600;
    line-height: 1.2;
  }
}
.statistics__description-term sup {
  font-size: 56%;
}
.statistics__description-details {
  margin: 0;
  line-height: 1.48;
}

.about-us {
  display: flex;
  height: 450px;
}
.about-us__item {
  width: 50%;
}
.about-us__img {
  width: 100%;
}
.about-us__content {
  justify-content: center;
  padding-left: 80px;
}
.about-us__text {
  width: 100%;
  max-width: 418px;
}
.about-us__title {
  margin-bottom: 9px;
}
.about-us__description {
  margin-bottom: 28px;
}

.help__text {
  grid-column: 1/span 3;
  margin-top: auto;
  margin-bottom: 21px;
}

.help-element {
  padding: 0 32px 40px 32px;
  height: 612px;
  background: linear-gradient(180deg, #fff 23.5%, #F2F6FB 23.5%);
  position: relative;
}
.help-element:nth-child(2) {
  grid-column: 5/span 4;
}
.help-element:nth-child(3) {
  grid-column: 9/span 4;
}
.help-element__title {
  margin-bottom: 16px;
  margin-top: auto;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.1;
  color: #1E1E1E;
}
.help-element__description {
  margin-bottom: 39px;
}
.help-element__btn {
  margin-right: auto;
}
.help-element__img {
  width: 384px;
  height: 308px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.news {
  padding-top: 64px;
  padding-bottom: 80px;
}
.news__header {
  margin-bottom: 32px;
}
.news__title {
  margin-bottom: 0;
}
.news-article {
  background: #F7F7F8;
}
.news-article:nth-of-type(1) {
  grid-column: 1/span 4;
}
.news-article:nth-of-type(2) {
  grid-column: 5/span 4;
}
.news-article:nth-of-type(3) {
  grid-column: 9/span 4;
}
.news-article__content {
  padding: 23px;
  order: 2;
}
.news-article__title {
  margin-bottom: 6px;
  font-size: 18px;
  font-weight: 600;
  line-height: 1.52;
  color: #000103;
}
.news-article__tags {
  display: flex;
  margin-bottom: 16px;
}
.news-article__tags-item {
  font-size: 12px;
  line-height: 1.58;
  position: relative;
}
.news-article__tags-item:not(:last-child):after {
  content: "";
  display: inline-block;
  width: 3px;
  height: 3px;
  margin: 0 6px;
  position: relative;
  top: -2px;
  border-radius: 50%;
  background-color: #D0D0D0;
}
.news-article__date {
  color: #8F9090;
}
.news-article__description {
  margin-bottom: 9px;
  font-size: 14px;
  line-height: 1.58;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
}
.news-article__img {
  order: 1;
  border-radius: 2px;
}

.download-catalog {
  height: 510px;
  background-image: url("../images/download-catalog-bg.jpg");
}
.download-catalog__inner {
  position: relative;
}
.download-catalog__content {
  width: 472px;
  padding: 40px;
  background: #F2F6FB;
  border-radius: 2px;
  position: absolute;
  top: 161px;
  left: 0;
}
.download-catalog__description {
  margin-bottom: 40px;
}

.brands {
  padding-bottom: 22px;
}
.brands__title {
  margin-bottom: 40px;
}
.brands__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -12px;
}
.brands__item {
  flex: 0 0 25%;
  padding: 0 12px;
  margin-bottom: 32px;
}
.brands__link {
  display: block;
  max-width: 260px;
  padding-left: 16px;
  border-left: 1px solid #E1E1E1;
}
.brands__icon {
  width: 180px;
  height: 48px;
  fill: #8F9090;
}
.brands__description {
  font-size: 14px;
  line-height: 1.58;
}

.footer {
  padding: 72px 0 60px;
  color: #EAEAEA;
}
.footer__wrapper-menu {
  padding-bottom: 87px;
  border-bottom: 1px solid #174463;
}
.footer__main {
  display: flex;
  justify-content: space-between;
  padding-top: 88px;
  font-size: 12px;
  line-height: 1.58;
}
.footer__menu {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  height: 374px;
}
.footer__item {
  width: 136px;
  margin-left: auto;
}
.footer__item:first-of-type {
  width: auto;
  margin-left: 0;
  margin-right: 65px;
}
.footer__item:nth-of-type(2) {
  width: 200px;
  margin-left: 0;
}
.footer__item:nth-of-type(2) .footer__item-title {
  margin-bottom: 19px;
}
.footer__item-list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: space-between;
  width: 555px;
  height: 282px;
}
.footer__item-list:first-child {
  margin-bottom: 23px;
  padding-right: 16px;
}
.footer__item-list--footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 24px;
  padding-right: 16px;
  height: auto;
  border-top: 1px solid #174463;
}
.footer__item-list--footer > .footer__item-element {
  margin-bottom: 0;
}
.footer__item-title {
  display: inline-block;
  margin-bottom: 24px;
  font-weight: 600;
  font-size: 18px;
  line-height: 1.52;
  color: #fff;
}
.footer__item-title--first {
  font-size: 21px;
  line-height: 1.2;
}
.footer__item-element {
  width: 200px;
  margin-bottom: 14px;
  font-size: 14px;
  line-height: 1.58;
}
.footer__item-link {
  padding-right: 3px;
}
.footer__item-icon {
  width: 11px;
  height: 14px;
  fill: transparent;
  stroke: #EAEAEA;
  position: relative;
  top: 2px;
}
.footer__logo {
  display: inline-block;
  margin-bottom: 29px;
}
.footer__copyright {
  margin-bottom: 24px;
}
.footer__link-list {
  display: flex;
}
.footer__link-item:not(:last-child) {
  margin-right: 24px;
}
.footer__link > span {
  font-size: 14px;
  font-weight: 700;
  line-height: 1.2;
  text-transform: uppercase;
}
.footer__main-right {
  width: 525px;
}
.footer__contacts {
  display: flex;
  margin-bottom: 22px;
}
.footer__contacts-phone {
  display: flex;
  flex-direction: column;
  margin-right: 64px;
}
.footer__contacts-phone-link {
  display: inline-block;
  margin-bottom: 1px;
  font-weight: 600;
  font-size: 21px;
  line-height: 1.2;
}
.footer__contacts-phone-paragraph {
  margin-left: 3px;
  font-size: 11px;
}
.footer__address {
  margin-bottom: 24px;
  font-size: 12px;
  font-style: normal;
}
.footer__info {
  font-size: 10px;
  color: #D0D0D0;
}
.footer__info-paragraph:first-child {
  margin-bottom: 9px;
}

.social {
  display: flex;
}
.social__item {
  margin-right: 6px;
}
.social__icon {
  width: 40px;
  height: 40px;
  fill: #fff;
}